@media (min-width: 1200px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl,
  .container-xxl {
    max-width: 1080px !important;
  }
  .container.c-blog,
  .container-lg.c-blog,
  .container-md.c-blog,
  .container-sm.c-blog,
  .container-xl.c-blog,
  .container-xxl.c-blog {
    max-width: 900px !important;
  }
}
@media (min-width: 1400px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl,
  .container-xxl {
    max-width: 1200px !important;
  }
  .container.c-blog,
  .container-lg.c-blog,
  .container-md.c-blog,
  .container-sm.c-blog,
  .container-xl.c-blog,
  .container-xxl.c-blog {
    max-width: 1000px !important;
  }
}
@media (max-width: 991px) {
  section {
    margin: 80px 0 !important;
  }
  header .row-header {
    padding: 10px 0;
  }
  header .row-header #nav-desk {
    display: none !important;
  }
  header .row-header .div-search i {
    border: none;
  }
  header .row-header #menu-mobile {
    display: flex !important;
    align-items: center;
  }
  header .row-header #menu-mobile i {
    font-size: 25px;
    color: #ec0001;
    margin-left: 10px;
  }
  header .row-header #menu-mobile i#icon-close {
    position: absolute;
    top: 20px;
    right: 20px;
    color: #404040;
  }
  .modal-content {
    width: 100% !important;
    height: 100% !important;
    left: 0 !important;
    top: 0 !important;
    border: none !important;
    border-radius: 0 !important;
  }
  #portfolio .container-categories {
    padding: 0;
  }
  #portfolio .portfolio-thumbnail {
    height: 300px;
  }
  #portfolio #portfolio-posts {
    grid-template-columns: 1fr;
    row-gap: 30px;
  }
  .slider-text {
    padding: 0 15px !important;
  }
  .slider-text .slider-content {
    font-size: 14px !important;
  }
  .category-btn {
    margin: 10px !important;
  }
  .blog-posts .post {
    padding: 0 10px !important;
  }
  .recommended-grid {
    grid-template-columns: 1fr !important;
    column-gap: 30px !important;
  }
  #blog-footer {
    margin-bottom: 0 !important;
  }
  #footer .footer-widgets .row-links nav {
    margin-top: 30px;
  }
  #footer .footer-widgets .row-links nav ul {
    gap: 15px;
  }
  #footer .footer-widgets .row-infos .div-info {
    text-align: center;
  }
  #footer .footer-widgets .row-infos .div-info a {
    justify-content: center;
  }
  #footer .footer-widgets .row-infos .div-info .relacionamento-item a {
    justify-content: center;
  }
  #footer .footer-widgets .row-infos .div-info:nth-child(n+2) {
    margin-top: 20px;
  }
  #page-building .img-topo {
    padding-left: 0;
  }
  #page-building .img-final {
    padding-right: 0;
  }
  #page-building .row-final {
    flex-direction: column-reverse;
  }
  #page-portfolio #portfolio-categories {
    flex-direction: column;
  }
  #page-portfolio #portfolio-categories h2 {
    margin: 15px 0;
  }
  #page-portfolio #portfolio-categories .categorias-portfolio {
    flex-direction: column;
  }
  #page-portfolio #portfolio-categories .categorias-portfolio .btns-categorias .category-btn {
    font-size: 14px !important;
  }
  #page-portfolio #portfolio-categories .categorias-portfolio .div-search-portfolio i {
    display: none;
  }
  #page-portfolio #portfolio-categories .categorias-portfolio .div-search-portfolio input {
    display: block;
    margin: 30px 0;
  }
  #page-blog .slider-blog-text {
    bottom: 50% !important;
    transform: translate(-50%, 50%) !important;
    width: 75%;
  }
  #page-blog .slider-blog-text h2 {
    font-size: 20px !important;
  }
  #page-blog .slider-blog-text .slider-blog-content {
    font-size: 12px;
  }
  #page-blog #blog {
    padding: 15px 0;
  }
  #page-blog #blog #blog-categories .div-btns {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    width: 100%;
    border: none;
  }
  #page-blog #blog #blog-categories .div-btns .blog-category-btn {
    margin-right: 0 !important;
    border-bottom: 2px solid #999999;
  }
  #page-blog #blog #blog-posts {
    grid-template-columns: repeat(2, 1fr);
  }
  #page-blog #blog #blog-posts .post a p {
    font-size: 14px !important;
  }
  #page-contato .fale-conosco {
    text-align: center;
  }
  #diferenciais #diferenciais-categories .categories-btns .category-diferenciais {
    margin: 0 !important;
  }
  #diferenciais #diferenciais-categories .categories-btns .category-diferenciais::before {
    bottom: -2px;
  }
  #diferenciais .div-diferenciais .slick-prev {
    left: -3% !important;
  }
  #diferenciais .div-diferenciais .slick-next {
    right: -3% !important;
  }
  #plantas-implantacao .thumbnail {
    height: 250px !important;
  }
  #plantas-implantacao #modalPlantasImplantacao .modal-content {
    padding: 10px 0 !important;
  }
  #plantas-implantacao #modalPlantasImplantacao .modal-content .modal-header {
    justify-content: center !important;
    margin-bottom: 15px;
  }
  #plantas-implantacao #modalPlantasImplantacao .modal-content .modal-menu {
    text-align: center;
  }
  #plantas-implantacao #modalPlantasImplantacao .modal-content .navbar-plantas {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    border: none !important;
  }
  #status .img-status img {
    height: 500px !important;
  }
  #areas-comuns .slider-areas-comuns {
    padding: 0;
  }
}
@media (max-width: 475px) {
  #incorporadora p {
    font-size: 20px;
  }
  #secao-esquerda h2,
  #secao-direita h2 {
    font-size: 20px !important;
  }
  #secao-esquerda p,
  #secao-direita p {
    font-size: 14px !important;
  }
  #footer .footer-widgets .row-links nav ul {
    gap: 10px;
  }
  #diferenciais #diferenciais-categories {
    flex-direction: column;
  }
  #diferenciais #diferenciais-categories h2 {
    margin-bottom: 30px;
  }
  #diferenciais #diferenciais-categories .btns-categorias .diferenciais-category-btn {
    margin: 0 10px;
  }
  #diferenciais .div-diferenciais .slick-prev {
    left: -2% !important;
  }
  #diferenciais .div-diferenciais .slick-next {
    right: -2% !important;
  }
  #plantas-implantacao .thumbnail {
    height: 150px !important;
  }
}
@media (min-width: 992px) {
  .ps-lg-6 {
    padding-left: 4rem !important;
  }
}